<template>
  <div id="encyclopedias">
    <new-layout>
      <template slot="top">
        <announce-header>评估</announce-header>
      </template>
      <template slot="center">
        <ul>
          <li>
            <span>性别</span>
            <span class="mt15">
                  <van-radio-group v-model="sex">
                    <van-radio name="1">男</van-radio>
                    <van-radio name="0">女</van-radio>
                  </van-radio-group>
           </span>
          </li>
          <li>
            <span>年龄</span>
            <span><input type="text" v-model="age" placeholder="请输入年龄"/></span>
          </li>
          <li>
            <span>体重</span>
            <span><input type="text" v-model="weight" placeholder="请输入体重"/></span>
          </li>
          <li>
            <span>身高</span>
            <span><input type="text" v-model="height" placeholder="请输入身高"/></span>
          </li>

          <li>
            <span>高压（收缩压）</span>
            <span><input type="text" v-model="sbp" placeholder="请输入高压"/></span>
          </li>
          <li>
            <span> 低压（舒张压）</span>
            <span><input type="text" v-model="dbp"  placeholder="请输入低压"/></span>
          </li>
          <li>
            <span>餐后血糖</span>
            <span><input type="text" v-model="pbg"  placeholder="请输入餐后血糖"/></span>
          </li>
          <li>
            <span>空腹血糖</span>
            <span><input type="text" v-model="fbc"  placeholder="请输入空腹血糖"/></span>
          </li>
          <li>
            <span>腰围 单位:CM</span>
            <span><input type="text" v-model="waistline"  placeholder="请输入腰围"/></span>
          </li>
          <li>
            <span>患糖尿病</span>
            <span  class="mt15">
                   <van-radio-group v-model="diabetes">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                   </van-radio-group>
            </span>
          </li>
          <li>
            <span>发作过中风或脑缺血</span>
            <span  class="mt15">
                  <van-radio-group v-model="strokeOrTia">
                    <van-radio name="1">是</van-radio>
                    <van-radio name="0">否</van-radio>
                  </van-radio-group>
              </span>
          </li>
          <li>
            <span>空腹血清总胆固醇</span>
            <span><input type="text" v-model="tc_mmol" placeholder="请输入空腹血清总胆固醇"/></span>
          </li>
          <li>
            <span>吸烟</span>
            <span  class="mt15">
                     <van-radio-group v-model="smoke">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                     </van-radio-group>
              </span>
          </li>
          <li>
            <span>低密度脂蛋白</span>
            <span><input type="text" v-model="idl"  placeholder="请输入低密度脂蛋白"/></span>
          </li>
          <li>
            <span>高密度脂蛋白</span>
            <span><input type="text" v-model="hdl"  placeholder="请输入高密度脂蛋白"/></span>
          </li>
          <li>
            <span>CVD家族史</span>
            <span class="mt15">
                   <van-radio-group v-model="family_cvd">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                   </van-radio-group>
               </span>
          </li>
           <li>
            <span> 每天锻炼30分钟以上</span>
            <span class="mt15">
                    <van-radio-group v-model="sports">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                   </van-radio-group>
              </span>
          </li>
          <li>
            <span>每天吃水果或蔬菜</span>
            <span class="mt15">
                <van-radio-group v-model="vegOrFruits">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
               </van-radio-group>
            </span>
          </li>
          <li>
            <span>服用降压药</span>
            <span class="mt15">
                <van-radio-group v-model="dyazide">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
               </van-radio-group>
            </span>
          </li>
          <li>
            <span>高血糖</span>
            <span class="mt15">
                     <van-radio-group v-model="hyperglycemia">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                   </van-radio-group>
              </span>
          </li>
           <li>
            <span>有糖尿病家族史</span>
            <span class="mt15">
                    <van-radio-group v-model="family_diabetes">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                   </van-radio-group>
             </span>
          </li>
           <li>
            <span>连续6个月以上服用激素类药品</span>
            <span class="mt15">
                      <van-radio-group v-model="hormone">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                      </van-radio-group>
              </span>
          </li>
           <li>
            <span>45岁前绝经</span>
            <span class="mt15">
                 <van-radio-group v-model="menopause">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                </van-radio-group>
              </span>
          </li>
          <li>
            <span>饮酒</span>
            <span class="mt15">
                    <van-radio-group v-model="drinking">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                   </van-radio-group>
              </span>
          </li>
          <li>
            <span>腹泻、腹痛或大便习惯</span>
            <span class="mt15">
                    <van-radio-group v-model="diarrhea">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                   </van-radio-group>
              </span>
          </li>
          <li>
            <span>不经常晒太阳</span>
            <span class="mt15">
                   <van-radio-group v-model="rarelyBask">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                   </van-radio-group>
              </span>
          </li>
          <li>
            <span>不经常参加运动锻炼</span>
            <span class="mt15">
                   <van-radio-group v-model="rarelysports">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                   </van-radio-group>
              </span>
          </li>
          <li>
            <span>有质疏松家族史</span>
            <span class="mt15">
                    <van-radio-group v-model="family_osteoporosis">
                      <van-radio name="1">是</van-radio>
                      <van-radio name="0">否</van-radio>
                   </van-radio-group>
              </span>
          </li>
          <li>
            <span>有高血压家族史</span>
            <span class="mt15">
                     <van-radio-group v-model="family_hypertension">
                      <van-radio name="0">父母皆无</van-radio>
                      <van-radio name="1">父或母有</van-radio>
                      <van-radio name="2">父母皆有</van-radio>
                    </van-radio-group>
           </span>
          </li>
        </ul>

        <div class="button-div" @click="submit()">提交</div>
      </template>
     <template slot="bottom">

        <van-popup v-model="contentshow" position="right">

          <div @click="closeds()" id="cancel">取消</div>
             <div>
                <p><b>评估名称</b>：糖尿病风险评估</p>
               <p><b>风险等级</b>：<span :class="diabetesArray.riskLevel == '低风险'?'lowrisk':(diabetesArray.riskLevel == '中等风险'?'mediumrisk':'highrisk')">{{diabetesArray.riskLevel}}</span></p>
                <p><b>分析建议</b>：<!--{{diabetesArray.suggest}} --><span v-html="diabetesSuggest"></span></p>
             </div>
         <div>
            <p><b>评估名称</b>：高血压风险评估</p><!--.highrisk,.mediumrisk,.lowrisk-->
            <p><b>风险等级</b>：
                  <span :class="hypertension.riskLevel == '低风险'?'lowrisk':(hypertension.riskLevel == '中等风险'?'mediumrisk':'highrisk')">{{hypertension.riskLevel}}</span>
            </p>
            <p><b>分析建议</b>：<!--{{hypertension.suggest}}--> <span v-htnl="hypertensionSuggest"></span></p>
          </div>
          <div>
            <p><b>评估名称</b>：心房颤动后中风风险评估</p>
            <p><b>风险等级</b>：<span :class="af.riskLevel == '低风险'?'lowrisk':(af.riskLevel == '中等风险'?'mediumrisk':'highrisk')">{{af.riskLevel}}</span></p>

          <!-- <p><b>分析建议</b>：<span v-html="af.suggest"></span></p>-->
          <!-- <p><b>分析建议</b>：{{replaces(af.suggest)}}</p>-->
            <p><b>分析建议</b>： <span v-html="afSuggest"></span>  <!--{{afSuggest}}--></p>
          </div>
          <div>
            <p><b>评估名称</b>：冠心病风险评估</p>
            <p><b>风险等级</b>：<span :class="chd.riskLevel == '低风险'?'lowrisk':(chd.riskLevel == '中等风险'?'mediumrisk':'highrisk')">{{chd.riskLevel}}</span></p>
            <p><b>分析建议</b>：<!--{{chd.suggest}}--><span v-html="chdSuggest"></span></p>
          </div>
          <div>
            <p><b>评估名称</b>：缺血性心血管病风险评估</p>
            <p><b>风险等级</b>：<span :class="icvd.riskLevel == '低风险'?'lowrisk':(icvd.riskLevel == '中等风险'?'mediumrisk':'highrisk')">{{icvd.riskLevel}}</span></p>
            <p><b>分析建议</b>：<!--{{icvd.suggest}}--><span v-html="icvdSuggest"></span></p>
          </div>


          <div>
            <p><b>评估名称</b>：五年内糖尿病患者发生心脑血管并发症风险评估</p>
            <p><b>风险等级</b>：<span :class="cvd.riskLevel == '低风险'?'lowrisk':(cvd.riskLevel == '中等风险'?'mediumrisk':'highrisk')">{{cvd.riskLevel}}</span></p>
            <p><b>分析建议</b>：<!--{{cvd.suggest}}--><span v-html="cvdSuggest"></span></p>
          </div>
          <div>
            <p><b>评估名称</b>：骨质疏松症风险评估</p>
            <p><b>风险等级</b>：<span :class="osteoporosis.riskLevel == '低风险'?'lowrisk':(osteoporosis.riskLevel == '中等风险'?'mediumrisk':'highrisk')">{{osteoporosis.riskLevel}}</span></p>
            <p><b>分析建议</b>：<!--{{osteoporosis.suggest}}--> <span v-html="osteoporosisSuggest"></span></p>
          </div>



        </van-popup>


      </template>
    </new-layout>
  </div>
</template>

<script>
  import {RadioGroup,Radio} from 'vant';

  export default {
    data(){
      return {
        sex:'1',
        age:'47',
        weight:'95',
        height:'178',
        sbp:'156',
        dbp:'90',
        pbg:'16.1',
        fbc:'10.8',
        waistline:'100',
        diabetes:'1',
        strokeOrTia:'1',
        tc_mmol:'7.8',
        smoke:'1',
        idl:'4.12',
        hdl:'4.5',
        family_cvd:'1',
        sports:'1',
        vegOrFruits:'1',
        hyperglycemia:'1',
        family_diabetes:'1',
        hormone:'1',
        menopause:'1',
        drinking:'1',
        diarrhea:'1',
        rarelyBask:'1',
        rarelysports:'1',
        family_osteoporosis:'1',
        family_hypertension:'0',
        dyazide:'1',
        contentshow: false,
        results: [],
        diabetesArray:[],
        hypertension:[],
        af:[],
        chd:[],
        icvd:[],
        cvd:[],
        osteoporosis:[],
        afSuggest:'',
        diabetesSuggest:'',
        hypertensionSuggest:'',
        chdSuggest:'',
        icvdSuggest:'',
        cvdSuggest:'',
        osteoporosisSuggest:'',

      }
    },
    mounted:{

    },
    methods:{
      replaces(val) {
          return val.replace(/[@#]/g,"<br/>")
      },
      closeds() {
        this.contentshow = false;
      },
      async submit(){
        let params = {
          sex:this.sex,
          age:this.age,
          weight:this.weight,
          height:this.height,
          sbp:this.sbp,
          dbp:this.dbp,
          pbg:this.pbg,
          fbc:this.fbc,
          waistline:this.waistline,
          diabetes:this.diabetes,
          strokeOrTia:this.strokeOrTia,
          tc_mmol:this.tc_mmol,
          smoke:this.smoke,
          idl:this.idl,
          hdl:this.hdl,
          family_cvd:this.family_cvd,
          sports:this.sports,
          vegOrFruits:this.vegOrFruits,
          hyperglycemia:this.hyperglycemia,
          family_diabetes:this.family_diabetes,
          hormone:this.hormone,
          menopause:this.menopause,
          drinking:this.drinking,
          diarrhea:this.diarrhea,
          rarelyBask:this.rarelyBask,
          rarelysports:this.rarelysports,
          family_osteoporosis:this.family_osteoporosis,
          family_hypertension:this.family_hypertension,
          dyazide:this.dyazide,
        };

        let res = await this.$http.post(this.$HOST + '/open/xk/evaluate',params);
        if(res) {
          this.diabetesArray = JSON.parse(res.result.diabetes.result);
          this.diabetesSuggest = this.replaces(this.diabetesArray.suggest);

          this.hypertension = JSON.parse(res.result.hypertension.result);
          this.hypertensionSuggest=this.replaces(this.hypertension.suggest);

          this.af = JSON.parse(res.result.af.result);
          this.afSuggest= this.replaces(this.af.suggest);

          this.chd = JSON.parse(res.result.chd.result);
          this.chdSuggest =  this.replaces(this.chd.suggest);

          this.icvd = JSON.parse(res.result.icvd.result);
          this.icvdSuggest =this.replaces(this.icvd.suggest);


          this.cvd = JSON.parse(res.result.cvd.result);
          this.cvdSuggest = this.replaces(this.cvd.suggest);

          this.osteoporosis = JSON.parse(res.result.osteoporosis.result);
          this.osteoporosisSuggest = this.replaces(this.osteoporosis.suggest);

          this.contentshow = true;
         }

      }
    },

    created(){

    }
  }
</script>

<style scoped lang="scss">
  $height80: 80px;
  $bg-fff: #ffffff;
  #encyclopedias {
    background: #f3f3f3;
    .sugges_li {
      border-bottom: 1px dashed #999999;
      margin-bottom: 12px;
      padding: 10px;
    }
    .title {
      color: orange;
      font-size: 24px;
    }
    p:nth-child(2) {
      font-size: 18px;
    }
  }

  table {
    width: 96%;
    margin: auto;
    tr {
      height: 50px;
      line-height: 50px;
      td {
        font-size: 18px;
      }
      td:nth-child(2) {
        font-weight: bold;
        color: #333333;
      }
    }
  }

  .van-cell__title span {
    font-size: 18px !important;
  }

  .width100 {
    width: 100px;
  }

  header {
    font-size: 30px;
    background: #ffffff;
    text-indent: 18px;
    height: 80px;
    line-height: 80px;
    font-weight: bold;
  }

  .disease_detail {
    color: #089ec3;
    font-size: 15px;
    background: #ffffff;
    text-indent: 18px;
    height: 80px;
    line-height: 80px;
    font-weight: bold;
  }

  .suit {
    width: 30px;
    height: 30px;
    background: #68ba8a;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color: #ffffff;
    font-size: 14px;
  }

  .avoid {
    width: 30px;
    height: 30px;
    background: #d95d5d;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color: #ffffff;
    font-size: 14px;
  }

  ul {
    li {
      height: 140px;
      line-height: 140px;
      width: 750px;
      /*background: #00c000;*/
      display: grid;
      grid-template-columns: 430px 300px;
      grid-template-rows: 50px;
      span:first-child{
        font-size: 18px;
      }
      span:nth-child(2){
        display: inline-block;
        font-size: 18px;
        input[type=text]{
          height: 140px;
          text-align: center;
        }
      }
      .van-radio-group{
        display: inline-block;
        height: 0.2rem!important;
        margin-top: 30px;
        .van-radio{
          display: inline-block;
          float: left;
        }
      }
    }
    li:nth-child(2n){
      background: #efeded;
    }
   text-indent: 10px;
  }

  input{
    border: none;
    background: none;
  }

  .mt15{
    margin-top: -25px;
    height: 90px;
    line-height: 90px;
  }

  .button-div{
    width: 450px;
    height: 90px;
    line-height: 90px;
    color: #ffffff;
    background:#406ea7;
    margin-left: 150px;
    margin-top: 250px;
    margin-bottom: 140px;
    text-align: center;
    border-radius: 10px;
  }
  .layout--bottom{
    z-index: 0;
  }
  .van-radio-group{
    height: 90px!important;
  }

  .van-radio{
    height: 100px;

  }
  .van-popup div{
   margin-bottom: 100px;
  }

.highrisk,.mediumrisk,.lowrisk{
    width: 166px;
    height: 62px;
    line-height: 62px;
    color: #ffffff;
    display: inline-block;
    text-align: center;
    border-radius: 10px;
}

  .highrisk{
    background: #ff4444;
  }
  .mediumrisk{
    background: #ed9e1b;
  }
  .lowrisk{
    background: #06a05c;
  }
  p{
    line-height:80px;

  }
  .van-popup div{
    width: 92%;
    margin: auto;
    border-bottom: 1px solid #d4d4d4;
    margin-bottom: 30px;
  }
/deep/.van-radio__input{
    height: 0.9rem!important;
   }

 /deep/ #cancel{
    text-align: right;
    height: 120px;
    line-height: 120px;
    background: white;
}

/deep/.van-radio .van-radio__input{
    vertical-align: sub!important;

  }

  .van-popup--right div:last-child{
    padding-bottom: 7vh;
  }


  .text-wrapper {
    white-space: pre-wrap;
  }

</style>



